@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
*,
*::before,
*::after {
    box-sizing: border-box;
    text-decoration: none;
    margin: 0;
    padding: 0;
}

a:visited {
    color: black;
}

body, html {
    font-family: 'Open Sans', sans-serif;
    height: 100%;
    width: 100%;
    line-height: 1.5rem;
    font-size: 16px;
}

html {
  overflow-y: overlay;
}

.svg-gradient {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -9999;
}

.forms-ctn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50vw;
  margin: 0 auto;
  padding-top: 1rem;
  padding-bottom: 5rem;
}

.submit-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: none;
  text-align: left;
  outline: none;
}

button {
  display: inline-block;
  border: none;
  padding: 1rem 2rem;
  text-decoration: none;
  background: rgb(68, 91, 223);
  color: #ffffff;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  transition: background 0.15s linear;
  -webkit-transition: background 0.15s linear;
  -moz-transition: background 0.15s linear;
  -ms-transition: background 0.15s linear;
  -o-transition: background 0.15s linear;
}

button:disabled,
button[disabled],
button:disabled:hover {
  background: rgb(77, 101, 240);
  color: #f1f1f1;
  cursor: auto;
}

.fa-pencil-square {
  padding: 0 0.5em;
}

textarea {
  line-height: 1.2rem;
}

.preview-ctn {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 2rem;
  width: 60vw;
  margin: 0 auto;
}
  
.general-info-preview {
    position: relative;
    font-size: 1.2rem;
    background-color: rgba(30, 35, 80, 0.205);
    color: rgb(51, 51, 51);
    line-height: 2.5rem;
    width: 100%;
}

.edit-cv-ctn {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(68, 91, 223);
    padding: 0.25rem;
    cursor: pointer;
    opacity: 0.6;
    transition: opacity 0.15s linear;
    -webkit-transition: opacity 0.15s linear;
    -moz-transition: opacity 0.15s linear;
    -ms-transition: opacity 0.15s linear;
    -o-transition: opacity 0.15s linear;
}

.edit-cv-ctn:hover {
    opacity: 1;
} 

.fa-pen-square {
    font-size: 1.75rem;
    color: rgb(255, 255, 255);
    margin: 0.25rem;
}

.profile-info-ctn {
    padding: 4rem 2rem 1rem 2rem;
}
  
.info-preview-ctn {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    background-color: rgb(245, 245, 245);
    padding: 2rem 2rem 0 2rem;
    color: rgb(49, 49, 49);
    width: 100%;
}

.info-ctn {
    display: flex;
    color: #383838;
    padding: 1rem 0;
    width: 100%;
}

.info-desc {
   width: 100%;
   line-height: 1.8rem;
}

ul {
    text-align: left;
    list-style-position: inside;
}

.date-info-ctn {
    width: 20rem;
    color:rgb(75, 75, 75);
}

.preview-border {
    border: 0.5px rgba(0, 0, 0, 0.199) solid;
    width: 100%;
    margin: 0.5rem 0 1rem 0;
}


@media only screen and (max-width: 768px) {
    .preview-ctn {
        width: 100vw;
    }
}




  


.submit-btn-ctn {
    padding: 1em;
}

.submit-btn {
    background: rgb(77, 101, 240);
    padding: 0.75em 1em;
}

.submit-btn:hover {
    background: rgb(85, 108, 240);
}

.btn-content {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
}

.btn-content p {
    padding-right: 0.5em;
}

.fa-check-square {
    padding: 0;
    font-size: 1.8rem;
}
.btn-ctn {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    
}

.collapsible {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    width: 45rem;
    background-color: rgb(228, 228, 228);
    color: rgb(0, 0, 0);
    cursor: pointer;
    padding: 1rem 1.5rem;
    margin: 1rem 0 0 0;
    border: none;
    text-align: left;
    outline: none;
    transition: 0.2s linear;
    -webkit-transition: 0.2s linear;
    -moz-transition: 0.2s linear;
    -ms-transition: 0.2s linear;
    -o-transition: 0.2s linear;
    font-size: 1.2rem;
    overflow: hidden;
}

.collapsible div {
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.active, .collapsible:hover {
    background-color: rgb(215, 215, 215);
}

.content {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(245, 245, 245);
    width: 45rem;
    max-height: 0;
    overflow: hidden;
    visibility: hidden;
    transition: transform 0.1s linear;
    -webkit-transition: transform 0.1s linear;
    -moz-transition: transform 0.1s linear;
    -ms-transition: transform 0.1s linear;
    -o-transition: transform 0.1s linear;
    transform: scale(0.99);
    -webkit-transform: scale(0.99);
    -moz-transform: scale(0.99);
    -ms-transform: scale(0.99);
    -o-transform: scale(0.99);
}

.content_active {
    visibility: visible;
    max-height: 1000px;
    transform: none;
    -webkit-transform: none;
    -moz-transform: none;
    -ms-transform: none;
    -o-transform: none;
    overflow: hidden;
    padding: 0.5rem 1.5rem; 
    box-shadow: 3px 8px 10px -3px rgba(0, 0, 0, 0.15);
}

.form-ctn {
    display: grid;
    padding: 0.5em;
    width: 100%;
}
  
.input-ctn {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 20px;
}
  
.add-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0.5rem;
    margin: 0.5rem 0;
    background-color: rgb(77, 101, 240);
}
  
.add-btn:hover {
    background: rgb(61, 83, 212);
}
  
.form-ctn input {
    width: 100%;
    padding: 0.25rem;
    margin: 0.25em 0;
    font-size: 1rem;
}
  
.form-ctn label {
    font-weight: 600;
}

.rotate {
    transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -o-transform: rotate(-180deg);
    transition: all 0.1s linear;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
}

.fa-chevron-down {
    transition: all 0.1s linear;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
}

.work-exp-tab-ctn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(240, 240, 240);
    cursor: pointer;
    margin-bottom: 1rem;
}

.work-exp-tab-info {
    margin: 0.25rem 0;
    padding: 0.5rem 1rem;
}


.work-exp-tab-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

.work-exp-tab-actions .fas {
    opacity: 0.6;
    padding: 0 1rem;
    font-size: 1.1rem;
    transition: all 0.05s linear;
    -webkit-transition: all 0.05s linear;
    -moz-transition: all 0.05s linear;
    -ms-transition: all 0.05s linear;
    -o-transition: all 0.05s linear;
}

.fa-trash-alt:hover {
    color: rgba(255, 0, 0, 0.700);
}

.fa-pen:hover {
    color: rgb(20, 60, 97);
}

@media only screen and (max-width: 768px) {
    .collapsible {
        width: 100vw;
    }

    .content {
        width: 100vw;
        
    }

    .content_active {
        width: 100vw;
        
    }

    .form-ctn-gen {
        width: 100vw;
    }
  }
